<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                placeholder="监测对象、设备名称、设备编号过滤"
                @callFunction="callFunction"
            ></toolbar>
            <div class="memo">注：冻结的摄像头，7天之后将被系统自动清理</div>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="siteshortname"
                    label="监测对象"
                    min-width="1"
                >
                </el-table-column>
                <el-table-column
                    prop="cameraname"
                    label="设备名称"
                    min-width="1"
                >
                </el-table-column>
                <el-table-column
                    prop="cameratype"
                    label="设备类型"
                    min-width="1"
                >
                    <nobr slot-scope="scope">
                        {{ mcameratype[scope.row.cameratype] }}
                    </nobr>
                </el-table-column>
                <el-table-column
                    prop="cameracode"
                    label="设备编号"
                    show-overflow-tooltip
                    min-width="2"
                ></el-table-column>
                <el-table-column prop="channel" label="信道" width="80">
                </el-table-column>
                <el-table-column
                    prop="devcode"
                    label="关联设备"
                    show-overflow-tooltip
                    min-width="1"
                ></el-table-column>
                <el-table-column prop="status" label="状态" min-width="1">
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.bdel"
                            v-if="canStatus"
                            :active-value="0"
                            active-text="正常"
                            :inactive-value="1"
                            inactive-text="冻结"
                            @change="
                                handleStatus($event, scope.$index, scope.row)
                            "
                        ></el-switch>
                        <div v-else>
                            {{ scope.row.bdel == 0 ? "正常" : "冻结" }}
                        </div>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="100" v-if="canEdit">
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <!-- <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        > -->
                    </template>
                </el-table-column>
            </el-table>

            <!--翻页-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="editForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="editForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备类型" prop="cameratype">
                                <el-select
                                    v-model="editForm.cameratype"
                                    filterable
                                    placeholder="请选择"
                                    @change="cameratypeChange"
                                >
                                    <el-option
                                        v-for="(value, key) in mcameratype"
                                        :key="key"
                                        :label="value"
                                        :value="Number(key)"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备名称" prop="cameraname">
                                <el-input
                                    v-model="editForm.cameraname"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item
                                :label="cameratypename"
                                prop="cameracode"
                            >
                                <el-input
                                    v-model="editForm.cameracode"
                                    auto-complete="off"
                                    maxlength="200"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="信道" prop="channel">
                                <el-input
                                    v-model="editForm.channel"
                                    auto-complete="off"
                                    maxlength="5"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="备选参数" prop="cameraparam">
                                <el-input
                                    v-model="editForm.cameraparam"
                                    auto-complete="off"
                                    maxlength="500"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="关联设备编号" prop="devcode">
                                <el-input
                                    v-model="editForm.devcode"
                                    auto-complete="off"
                                    maxlength="100"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="addForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="addForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备类型" prop="cameratype">
                                <el-select
                                    v-model="addForm.cameratype"
                                    filterable
                                    placeholder="请选择"
                                    @change="cameratypeChange"
                                >
                                    <el-option
                                        v-for="(value, key) in mcameratype"
                                        :key="key"
                                        :label="value"
                                        :value="Number(key)"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备名称" prop="cameraname">
                                <el-input
                                    v-model="addForm.cameraname"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item
                                :label="cameratypename"
                                prop="cameracode"
                            >
                                <el-input
                                    v-model="addForm.cameracode"
                                    auto-complete="off"
                                    maxlength="200"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="信道" prop="channel">
                                <el-input
                                    v-model="addForm.channel"
                                    auto-complete="off"
                                    maxlength="5"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="备选参数" prop="cameraparam">
                                <el-input
                                    v-model="addForm.cameraparam"
                                    auto-complete="off"
                                    maxlength="500"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="关联设备编号" prop="devcode">
                                <el-input
                                    v-model="addForm.devcode"
                                    auto-complete="off"
                                    maxlength="100"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getCamerapagelist,
    getCameraGroups,
    switchCamera,
    editCamera,
    addCamera,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import { mcameratype } from "../../assets/dic/dic";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                cameratype: [
                    {
                        required: true,
                        message: "请选择设备类型",
                        trigger: "blur",
                    },
                ],
                cameracode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                cameraname: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                cameratype: [
                    {
                        required: true,
                        message: "请选择设备类型",
                        trigger: "blur",
                    },
                ],
                cameracode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                cameraname: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canStatus: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            mcameratype: mcameratype,
            cameratypename: "设备编号",
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getCamerapagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getCameraGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        //删除
        // handleDel(index, r) {
        //     let row = r;
        //     if (!row) {
        //         this.$message({
        //             message: "请选择要删除的一行数据！",
        //             type: "error",
        //         });

        //         return;
        //     }
        //     this.$confirm("确认删除该记录吗?", "提示", {
        //         type: "warning",
        //     })
        //         .then(() => {
        //             this.listLoading = true;
        //             let para = { id: row.CAMERAID };
        //             removeCamera(para).then((res) => {
        //                 this.listLoading = false;
        //                 if (res.success) {
        //                     this.$message({
        //                         message: "删除成功",
        //                         type: "success",
        //                     });
        //                 } else {
        //                     this.$message({
        //                         message: res.msg,
        //                         type: "error",
        //                     });
        //                 }

        //                 this.getPageList(this.page);
        //                 this.getGroupCounts();
        //             });
        //         })
        //         .catch(() => {});
        // },
        //显示编辑界面
        handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.groupChange(row.GROUPID);
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
        },
        //冻结解冻
        handleStatus($event, index, row) {
            let para = { CAMERAID: row.CAMERAID, bdel: $event };
            switchCamera(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "操作成功",
                        type: "success",
                    });
                    this.getPageList(this.page);
                    this.getGroupCounts();
                } else {
                    // let r = row;
                    // r.status = row.status == 0 ? 1 : 0;
                    // this.Groups[index] = r;
                    this.$message({
                        message: "操作失败",
                        type: "error",
                    });
                    this.getPageList(this.page);
                    this.getGroupCounts();
                }
            });
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    console.log(JSON.stringify(para));
                    editCamera(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addCamera(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
        cameratypeChange(val) {
            if (val == "4") {
                this.cameratypename = "流地址";
            } else {
                this.cameratypename = "设备编号";
            }
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleStatus") {
                this.canStatus = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
.memo {
    color: red;
    position: absolute;
    right: 30px;
    top: 30px;
    font-size: 13px;
}
</style>


